.nav {
    grid-area: n;
    border-radius: 20px;
    padding: 10px;
    display: grid;
    justify-content: start;
    gap: 10px;
    background-color: rgb(188, 197, 209);
}

.item a  {
    color: black;
    font-weight: 600;
    text-decoration: none;
}

.item a.activeLink {
    color: #e221a8;
}